@btn-border-radius: 0px;

.component__container {
    align-items: center;
}

// Class applies alternative background color and component styling
body {
    background: #f3f2f2 url("assets/cubes.png");
    background-position: 5px 5px;
    font-family: @body-family;
    font-weight: 400; // Changed by Jack Schofield to improve readability
    color: @font-color;
    font-size: 17px;
    line-height: 27px;
}

b,
strong {
    font-weight: bold;
}

#wrapper {
    margin-left: auto;
    margin-right: auto;
    max-width: @max-width;
    padding-top: 0px;
}

.o_page__background {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    overflow: hidden;
    z-index: -1;
}

.o_page__background:before {
    content: " ";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: -webkit-linear-gradient(305deg,
            rgba(var(--backgroundColorRGB), 0) 20%,
            rgba(var(--backgroundColorRGB), 0.6) 40%,
            rgba(var(--backgroundColorRGB), 1) 80%),
        -webkit-linear-gradient(270deg, rgba(var(--backgroundColorRGB), 0) 20%, rgba(var(--backgroundColorRGB),
                0.6) 40%, rgba(var(--backgroundColorRGB), 1) 80%);
    background: linear-gradient(145deg,
            rgba(var(--backgroundColorRGB), 0) 20%,
            rgba(var(--backgroundColorRGB), 0.6) 40%,
            rgba(var(--backgroundColorRGB), 1) 80%),
        linear-gradient(180deg,
            rgba(var(--backgroundColorRGB), 0) 20%,
            rgba(var(--backgroundColorRGB), 0.6) 40%,
            rgba(var(--backgroundColorRGB), 1) 80%);
}

.o_page__background-image {
    min-width: 100vw;
    max-width: none;
    width: auto;
    min-height: 100vh;
    max-height: none;
    height: auto;
    object-fit: cover;
}

.nav {
    margin-left: auto;
    margin-right: auto;
    max-width: @max-width;
    position: sticky;
    height: @nav-logo-height;

    &__inner {
        height: inherit;
    }

    &__btn {
        background-color: @nav;
        color: @nav-icon;
        height: inherit;
    }

    &__back-btn {
        & when (@hide-nav =true) {
            display: none;
        }
    }

    &__home-btn {
        .u-float-left;

        & when (@hide-nav =true) {
            display: none;
        }
    }

    &__home-btn .icon {
        .icon-home;
    }
}

.drawer__item-title {
    font-family: @body-family;
}

.menu__title {
    color: @font-color;
}

.page {
    &__title {
        color: @font-color;
    }

    &__header {
        display: none;
    }
}

.footer {
    background-color: @footer;
    color: @footer-inverted;
    @heading-color: @footer-inverted;
    @heading-color-inverted: @greyDark;

    .graphic__attribution {
        display: none;
    }

    .block__title {
        color: @footer-inverted;
    }

    .component__title {
        color: @footer-inverted;
        font-weight: 600;
    }
    

    .graphic {
        &__inner {
            text-align: center;

            img {
                max-width: 75%;
                min-width: auto;
                margin-left: auto;
                margin-right: auto;
            }
        }
    }

    .footer-logo {
        text-align: left;

        @media all and (max-width: @device-width-medium) {
            text-align: center;
            padding-bottom: 20px;
        }

        @media all and (max-width: @device-width-small) {
            padding-bottom: 0px;
        }

        img {
            content: url("@{footer-logo}");
            max-height: 70px;
            min-width: auto;
            display: inline-block;
        }
    }

    .button {
        padding: 1rem;
        background-color: @footer-inverted;
        color: @footer;
    }

    a {
        &.button {
            color: @footer;
        }
    }
}

.min-width-auto img {
    min-width: auto;
    margin-left: auto;
    margin-right: auto;
}

.pagelevelprogress {
    &__indicator {
        width: 7rem;
    }

    &__indicator-inner {
        height: 0.5rem;
    }
}

blockquote {
    font-size: 1.5rem;
}

.about-credit p {
    display: inline;
}

.graphic__widget:has(> span.has-attribution) {
    overflow: hidden;
}

.gmcq__attribution {
    position: absolute;
}

.has-attribution {
    overflow: hidden;

    .component__attribution {
        overflow: hidden;
        padding: 0;
        padding-left: 0.5rem;
    }
}

.has-attribution:hover .component__attribution {
    height: fit-content;
    -webkit-transition: height, 0.5s linear;
    -moz-transition: height, 0.5s linear;
    -ms-transition: height, 0.5s linear;
    -o-transition: height, 0.5s linear;
    transition: height, 0.5s linear;
    padding-bottom: 0.2rem;
}

.component__attribution {
    height: 0px;
    -webkit-transition: height, 0.5s linear;
    -moz-transition: height, 0.5s linear;
    -ms-transition: height, 0.5s linear;
    -o-transition: height, 0.5s linear;
    transition: height, 0.5s linear;
}

a {
    color: inherit;

    &.button {
        padding: 1rem;
        text-decoration: underline;
        display: inline-block;
        margin: 0.2rem;
    }
}

.graphic__attribution a {
    color: white;
}

.socialbuttons-component {
    position: relative;
    text-align: right;
}

#social-icons {
    .social {
        img {
            min-width: 0%;
            display: inline-block;
        }
    }
}

.about-section {
    font-size: 0.8em;

    a {
        text-decoration: underline;
        cursor: pointer;
    }

    p {
        margin: 0px;
    }

    @media all and (max-width: @device-width-medium) {
        text-align: center;
    }
}

.about-credit {
    text-align: center;
    margin-bottom: 10px;
}

.navigation {
    img.navigation-logo {
        content: url("@{navigation-logo}");
        float: left;
        height: @nav-logo-height;
        padding-top: @nav-logo-padding-top;
        padding-left: @nav-logo-padding-left;

        @media all and (max-width: @device-width-small) {
            height: @nav-logo-height-mobile;
            padding-top: @nav-logo-padding-top-mobile;
            padding-left: @nav-logo-padding-left-mobile;
        }
    }
}

