.setNormal(@color, @color-inverted) {
    background-color: @color-inverted;
    @heading-color: @greyDark;
    @heading-color-inverted: @white;

    .button {
        padding: 1rem;
        background-color: @color;
        color: @color-inverted;
    }

    a {
        &.button {
            color: @color-inverted;
        }
    }

    table {
        margin: auto;
        width: 100%;
        overflow: hidden;
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
        border-radius: 0px !important;
        color: black !important;
        overflow-wrap: anywhere;
    }

    .levels td:nth-child(2) {
        color: black;
    }

    table tr {
        background-color: #DDDDDD
    }

    table th,
    table td {
        padding: 1em;
        text-align: left;
    }

    table th {
        background-color: #DDDDDD;
        color: white;
        font-weight: bold;
    }


    table tr:last-child td {
        border-bottom: none;
    }


    table td:first-child {
        font-weight: bold;
    }

    @media (max-width: 768px) {
        table {
            display: block;
            overflow-x: auto;
            white-space: nowrap;
        }

        table thead {
            display: none;
        }

        table tr {
            display: inline-block;
            vertical-align: top;
            margin: 0 0.25em;
            border: none;
        }

        table td {
            display: block;
            border: none;
        }

        table td:first-child {
            margin-top: 0.5em;
            font-weight: normal;
        }

        table td:last-child {
            margin-bottom: 0.5em;
        }

        table th,
        table td {
            border: 3px solid white;
            border-radius: 5px;
        }

    }

    .border-card-box {
        margin: 0 auto;
        padding: 2rem;
        background-color: #f9f9f9;
        color: black;
        border-radius: 10px;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
        border: 5px solid #0077b6;
    }

    .border-bottom {
        border-bottom: 2px solid @color;
        padding-bottom: 1rem;
    }

    .bold-color-text {
        font-weight: 600;
        color: @color;
    }


    .callout-border-bold-list {
        margin: 1rem 0;
        padding-left: 1rem;
        border-left: 5px solid @color-inverted;
    }

    .callout-border-bold-list li {
        line-height: 3;
    }

    .color-box {
        padding-inline: 2rem;
        padding-block: 1.7rem;
        border-radius: 5px;
        box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.05);
        background-color: @color;
        color: @font-color-inverted;
    }

    .orange-caution-callout {
        background-color: #fff1e6;
        padding: 1rem;
        margin-block: 1rem;
        color: #d43f00;
    }

    .blue-caution-callout {
        background-color: #e0f7fa;
        padding: 1rem;
    }

    .styled-links {
        list-style-type: none;
        padding: 0;
        margin: 0;
    }

    .styled-links li {
        margin-bottom: 10px;
    }

    .styled-links a {
        display: block;
        padding: 10px 15px;
        background-color: #f7f7f7;
        /* Light Grey Background */
        border-left: 5px solid #0961b6;
        /* Blue Left Border */
        color: #333;
        /* Dark Grey Text */
        border-radius: 5px;
        font-weight: bolder;
        transition: background-color 0.4s, color 0.4s;
        /* Transition effect */
    }

    .two-by-two-grid {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 1.5rem;
        padding: 0;
    }

    .span-2-grid-item {
        grid-column: span 2;
    }

    .highlight-white-light {
        padding: 1rem;
        margin: 1rem 0;
        border: 1px solid #d6d6d6;
        border-radius: 5px;
        background-color: #fff;
        color: black;
    }

    .grey-italic-caution {
        background-color: #e8e8e8;
        padding: 1rem;
        margin-top: 3rem;
        border-radius: 5px;
        font-style: italic;
    }

    .inline-links {
        border-bottom: 1px dashed @color-inverted;
    }

    .large-card-box {
        padding: 2rem;
        background: #f9f9f9;
        border-radius: 10px;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
        list-style-type: none;
        color: black;
    }

    .sub-header h3 {
        border-bottom: 2px solid @color-inverted;
        padding-bottom: 0.5rem;
        margin-bottom: 1rem;
        font-weight: 600;
    }

    .small-quote {
        font-style: italic;
        margin: 0.5rem 0;
        padding: 0.5rem;
        border-left: 4px solid @color;
        font-size: large;
        list-style-type: none;
    }

    .block__title {
        color: @font-color;
    }

    .component__title {
        color: @font-color;
        font-weight: 600;
        padding-bottom: 30px;
    }

    .component__title-inner {
        border-bottom: 2px solid @color;
    }

    .intro {
        border-bottom: 2px solid @color;
    }

    .narrative {
        &__content {
            background-color: @color;
            padding: 1rem;
            color: @font-color-inverted;
        }

        &__content-body {

            a,
            a:visited,
            a:hover {
                color: inherit;
            }
        }

        &__controls {
            background-color: @color;
            color: @color-inverted;
        }

        &__progress {
            border: 1px solid @color;
            background-color: @color;
        }

        .is-selected {
            background-color: @color-inverted;
        }
    }

    .graphic-component {
        &.matchblock {
            .graphic__inner img {
                background-color: @color;
            }
        }
    }

    .accordion {
        &-item {
            border: 3px solid @color;
        }

        &-item__btn {
            border-radius: 0px;
            background-color: @color;

            .no-touch & {
                &:hover {
                    background-color: darken(@color, 20%);
                }

                &.visited:hover {
                    background-color: darken(@color, 20%);
                }
            }
        }

        &-item__content {
            border-radius: 0px;
            background-color: @color-inverted;
            color: @font-color;
        }

        &-item__btn:hover {
            background-color: darken(@color, 20%);
        }
    }

    .mcq-item {
        label {
            background-color: @color;
            border-radius: 0px;

            .no-touch & {
                &:hover {
                    background-color: darken(@color, 20%);
                }

                &.visited:hover {
                    background-color: darken(@color, 20%);
                }
            }
        }

        .is-disabled {
            color: @color-inverted;
            background-color: @color;
        }

        .is-selected {
            background-color: darken(@color, 20%);
        }
    }

    .matching {
        .dropdown__btn {
            background-color: @color;
            color: @color-inverted;
        }

        .dropdown-item {
            background-color: @color-inverted;
            color: @font-color;
            border-bottom: 1px solid @color;
            border-left: 1px solid @color;
            border-right: 1px solid @color;
        }

        .dropdown-item:hover {
            background-color: darken(@color-inverted, 30%);
        }
    }

    .hotgraphic {
        &__pin:not(.has-pin-image) {
            background-color: @color;
            color: @color-inverted;
        }
    }

    .dragndrop-inner .dragndrop-answers .dragndrop-answer {
        background: @color-inverted;
        color: @color;
        border: 1px solid @color;
        box-shadow: none;
    }
}

