            margin-bottom: 0.5em;
        }

        table th,
        table td {
            border: 3px solid white;
            border-radius: 5px;
        }

    }

    .border-card-box {
        margin: 0 auto;
        padding: 2rem;
        background-color: #f9f9f9;
        color: black;
        border-radius: 10px;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
        border: 5px solid #0077b6;
    }

    .border-bottom {
        border-bottom: 2px solid @color;
        padding-bottom: 1rem;
    }

    .bold-color-text {
        font-weight: 600;
        color: @color-inverted;
    }

    .callout-border-bold-list {
        margin: 1rem 0;
        padding-left: 1em;
        border-left: 5px solid @color-inverted;
    }

    .callout-border-bold-list li {
        line-height: 3;
    }

    .color-box {
        padding-inline: 2rem;
        padding-block: 1.7rem;
        border-radius: 5px;
        box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.05);
        background-color: @color-inverted;
        color: @font-color;
    }

    .orange-caution-callout {
        background-color: #fff1e6;
        padding: 1rem;
        margin-block: 1rem;
        color: #d43f00;
    }

    .blue-caution-callout {
        background-color: #e0f7fa;
        padding: 1rem;
    }

    .styled-links {
        list-style-type: none;
        padding: 0;
        margin: 0;
    }

    .styled-links li {
        margin-bottom: 10px;
    }

    .styled-links a {
        display: block;
        padding: 10px 15px;
        background-color: #f7f7f7;
        /* Light Grey Background */
        border-left: 5px solid #0961b6;
        /* Blue Left Border */
        color: #333;
        /* Dark Grey Text */
        border-radius: 5px;
        font-weight: bolder;
        transition: background-color 0.4s, color 0.4s;
        /* Transition effect */
    }

    .two-by-two-grid {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 1.5rem;
        padding: 0;
    }

    .span-2-grid-item {
        grid-column: span 2;
    }

    .highlight-white-light {
        padding: 1rem;
        margin: 1rem 0;
        border: 1px solid #d6d6d6;
        border-radius: 5px;
        background-color: #fff;
        color: black;
    }

    .grey-italic-caution {
        background-color: #e8e8e8;
        padding: 1rem;
        margin-top: 3rem;
        border-radius: 5px;
        font-style: italic;
    }

    .inline-links {
        border-bottom: 1px dashed @color-inverted;
    }

    .large-card-box {
        padding: 2rem;
        background: #f9f9f9;
        border-radius: 10px;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
        list-style-type: none;
    }

    .sub-header h3 {
        border-bottom: 2px solid @color-inverted;
        padding-bottom: 0.5rem;
        margin-bottom: 1rem;
        font-weight: 600;
    }

    .small-quote {
        font-style: italic;
        margin: 0.5rem 0;
        padding: 0.5rem;
        border-left: 4px solid @color;
        font-size: large;
        list-style-type: none;
    }

    .button {
        padding: 1rem;
        background-color: @color-inverted;
        color: @color;
    }

    a {
        &.button {
            color: @color;
        }
    }

    .block__title {
        color: @font-color-inverted;
    }

    .component__title {
        color: @color-inverted;
        font-weight: 600;
        padding-bottom: 10px;
    }

      .component__title-inner {
        border-bottom: 2px solid @color-inverted;
    }
    .gmcq .gmcq__item-inner,
    .gmcq .gmcq__item,
    .gmcq .gmcq__item label .gmcq__answer-icon,
    .gmcq .gmcq__item label.selected .gmcq__item-inner {
        color: @color-inverted;
    }

    .narrative {
        &__content {
            background-color: @greyDark;
            padding: 1rem;
        }

        &__content-body {

            a,
            a:visited,
            a:hover {
                color: inherit;
            }
        }

        &__controls {
            background-color: @greyDark;
            color: @color-inverted;
        }

        &__progress {
            border: 1px solid @color-inverted;
            background-color: @color-inverted;
        }

        .is-selected {
            background-color: @color;
        }
    }

    .accordion {
        &-item {
            border: 3px solid @color-inverted;
        }

        &-item__btn {
            border-radius: 0px;
            background-color: @color-inverted;
            color: darken(@color, 10%);

            .no-touch & {
                &:hover {
                    background-color: darken(@color-inverted, 20%);
                }

                &.visited:hover {
                    background-color: darken(@color-inverted, 20%);
                }
            }
        }

        &-item__content {
            border-radius: 0px;
            background-color: @color;
            color: @color-inverted;
        }

        &-item__btn:hover {
            background-color: darken(@color-inverted, 20%);
        }
    }

    .mcq-item {
        label {
            background-color: @color-inverted;
            color: darken(@color, 10%);

            .no-touch & {
                &:hover {
                    background-color: darken(@color-inverted, 20%);
                }

                &.visited:hover {
                    background-color: darken(@color-inverted, 20%);
                }
            }
        }

        .is-disabled {
            background-color: darken(@color-inverted, 20%);
        }

        .is-selected {
            background-color: @color-inverted;
        }
    }

    .gmcq__item-option {
        background-color: @color-inverted;
        color: @color;
    }

    .matching {
        .dropdown__btn {
            background-color: @color-inverted;
            color: @color;
        }

        .dropdown-item {
            background-color: @color-inverted;
            color: @color;
            border-bottom: 1px solid @color;
            border-left: 1px solid @color-inverted;
            border-right: 1px solid @color-inverted;
        }

        .dropdown-item:hover {
            background-color: darken(@color-inverted, 30%);
        }
    }

    .hotgraphic {
        &__pin:not(.has-pin-image) {
            background-color: @color-inverted;
            color: @color;
        }
    }

    .dragndrop-inner .dragndrop-answers .dragndrop-answer {
        background: @color-inverted;
        color: @color;
        border: 1px solid @color-inverted;
        box-shadow: none;
    }
}

.hotgraphic {
    .spectrum-marker {
        width: 64px;
        background-color: transparent;
    }
}

.hotgraphic-popup {
    &__item-image {
        margin-left: auto;
        margin-right: auto;
        height: 400px;
        min-width: 10%;
    }
}

.block {
    &__inner {
        @media all and (max-width: @device-width-small) {
            padding: 2rem 0.5rem;
        }
    }

    &.hidden {
        display: none;
    }

    .text {
        &.ticks ul li {
            list-style-type: none;
            margin-left: 28px;
            white-space: pre-wrap;

            &:before {
                content: "";
                margin: 0 14px 0 -28px;
            }
        }
    }

    .graphic {
        &.mobile-hide {
            @media all and (max-width: @device-width-small) {
                display: none;
            }
        }

        &.intro-logo {
            .graphic__widget {
                text-align: center;
            }

            .graphic__inner img {
                padding-top: @theme-logo-top-padding;
                min-width: @theme-logo-width;
                max-width: @theme-logo-width;
                margin-left: auto;
                margin-right: auto;
            }

            .graphic__attribution {
                display: none;
            }
        }

        &.clipboard {
            .graphic__widget {
                text-align: center;
            }

            .graphic__inner img {
                max-height: 290px;
                min-width: 50%;
                margin-left: auto;
                margin-right: auto;
            }
        }

        &.white {
            .graphic__inner img {
                background-color: white;
            }
        }
    }
}

.component {
    &.is-left {
        @media all and (max-width: @device-width-small) {
            padding-bottom: 2rem;
        }
    }
}

.component__body a,
.component__instruction a {
    color: inherit;
}

.boxmenu__item-container-inner {
    @media all and (max-width: @device-width-medium) {
        margin: 0rem 1rem;
    }
}

.section-banner {
    .block__inner {
        padding: 2rem;
    }

    img {
        min-width: 0px;
        margin-left: auto;
        margin-right: auto;
    }
}

.divider-block {
    background-color: @background-color-inverted;
    color: @foreground-color-inverted;
    text-align: center;

    .block-title,
    .block-body,
    .block-instruction,
    .component-title,
    .component-body,
    .component-instruction {
        color: @foreground-color-inverted;
        text-align: center;
    }

    .component-inner {
        background-color: transparent;
    }
}

.dragndrop-container {
    .dragndrop-answers {
        color: @foreground-color;
    }
}

.dragndrop-widget {
    .btn__container {
        display: inline-block;
        width: 100%;
    }
}

iframe {
    border: none;
}

@media only screen and (max-width: 950px) {
    .moral-machine iframe {
        height: 1080px;
    }
}